  font-weight: 400;
  font-display: swap;
  src: url("../fonts/Roboto-Medium.woff2"), url("../fonts/Roboto-Medium.woff"); }

a {
  text-decoration: none; }

img {
  max-width: 100%;
  height: auto; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
          clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.page {
  height: 100%;
  scroll-behavior: smooth; }

.page__body {
  display: grid;
  grid-template-rows: -webkit-min-content 1fr -webkit-min-content;
  grid-template-rows: min-content 1fr min-content;
  -ms-flex-line-pack: start;
      align-content: start;
  min-height: 100%;
  margin: 0;
  background-color: #FFFFFF;
  font-family: 'PT Mono', 'Arial', sans-serif;
  font-weight: 400;
  font-size: 22px;
  line-height: 22px;
  color: #000000; }

.page-header {
  min-height: 950px;
  padding-top: 33px;
  padding-left: 25px;
  padding-right: 25px;
  background-color: #1B2D37;
  background-image: url(../../img/main-block-background-desktop.png); }
  @media (max-width: 1023px) {
    .page-header {
      min-height: 686px; } }
  @media (max-width: 767px) {
    .page-header {
      min-height: 661px;
      padding-left: 16px;
      padding-right: 16px; } }

.page-header__top-wrapper {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  max-width: 1411px;
  margin-left: auto;
  margin-right: auto; }

.page-header__logo {
  display: block;
  margin-right: auto; }
  @media (max-width: 767px) {
    .page-header__logo {
      width: 38px;
      height: 38px; } }

.page-header__contacts {
  margin: 0;
  margin-right: 30px;
  margin-left: auto; }

.page-header__back-call {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  width: 180px;
  height: 45px;
  border: 1px solid #FFFFFF;
  border-radius: 5px;
  font-family: 'Roboto Condensed';
  font-weight: bold;
  font-size: 14px;
  line-height: 16px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: #FFFFFF; }
  .page-header__back-call:focus, .page-header__back-call:hover {
    color: #00B2FF;
    border-color: #00B2FF; }
  .page-header__back-call:active {
    color: #66d1ff;
    border-color: #66d1ff; }
  @media (max-width: 1023px) {
    .page-header__back-call {
      display: none; } }

.header-contacts {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  list-style: none;
  padding: 0; }

.header-contacts__item:not(:last-child) {
  margin-right: 38px; }
  @media (max-width: 1023px) {
    .header-contacts__item:not(:last-child) {
      margin-right: 33px; } }
  @media (max-width: 767px) {
    .header-contacts__item:not(:last-child) {
      margin-right: 23px; } }

.header-contacts__link {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  font-family: 'PT Mono';
  font-weight: 400;
  font-size: 16px;
  line-height: 18px;
  color: #FFFFFF; }
  .header-contacts__link:hover, .header-contacts__link:focus {
    color: #00B2FF; }
  .header-contacts__link:active {
    color: #66d1ff; }

@media (max-width: 1023px) {
  .header-contacts__link:hover .header-contacts__icon,
  .header-contacts__link:focus .header-contacts__icon {
    fill: #FFFFFF; } }

.header-contacts__link:active .header-contacts__icon {
  fill: #99e0ff; }

.header-contacts__icon {
  fill: #00B2FF; }
  .header-contacts__icon--mail {
    margin-right: 20px; }
    @media (max-width: 1023px) {
